'use strict';

/**
* Integer square root.
*
* @module @stdlib/math-base-special-fast-uint32-sqrt
*
* @example
* var sqrtUint32 = require( '@stdlib/math-base-special-fast-uint32-sqrt' );
*
* var v = sqrtUint32( 9 >>> 0 );
* // returns 3
*
* v = sqrtUint32( 2 >>> 0 );
* // returns 1
*
* v = sqrtUint32( 3 >>> 0 );
* // returns 1
*
* v = sqrtUint32( 0 >>> 0 );
* // returns 0
*/

// MODULES //

var sqrtUint32 = require( './sqrt.js' );


// EXPORTS //

module.exports = sqrtUint32;
